body{
  background: #498CEC;
 
}

.App{

  
}

.container{
  max-width: 1300px;
  height: 800px;
  margin: 0 auto;
}





@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}



@media(max-width:1400px){
  .container{
    max-width: 1100px;
  }
}


@media(max-width:1400px){
  .container{
    max-width: 900px;
  }
}

@media(max-width:1000px){
  .container{
    max-width: 600px;
  }
}


@media(max-width:650px){
  .container{
    max-width: 480px;
  }
}


@media(max-width:550px){
  .container{
    max-width: 350px;
  }
}

@media(max-width:450px){
  .container{
    max-width: 300px;
  }
}